export class Widget {
    _id: string;
    widgetType: string;
    pageId: string;
    size: string;
    text: string;
    url: string;
    width: string;

    constructor(_id, type, pageId, size= '1', text = 'text', width = '100%', url = 'url') {
        this._id = _id;
        this.widgetType = type;
        this.pageId = pageId;
        this.size = size;
        this.text = text;
        this.width = width;
        this.url = url;
    }
}
